.bgimg{
  background-image: url(women_empowered.jpg);
  min-height: 95vh;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 0px;
}
.form_block{
  width: 300px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 25px;
  padding-right: 25px;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 5px;
    border: 1px solid;
      -webkit-box-shadow: -1px 4px 26px 11px rgba(0,0,0,0.75);
      -moz-box-shadow: -1px 4px 26px 11px rgba(0,0,0,0.75);
}
.form_block > #title{
  padding: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 30px;
  text-align: center;
  font-family: cursive;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  text-shadow: -1px -1px 0 rgba(0,0,0,.2);
}
.form_block > .body{
  padding: 10px;
  display: table;
}
.form_block:hover{
  background: rgba(0,0,0,0.8);
}
.body
{
  color:rgb(245, 245, 245);
  font-style: italic ;
  font-size: 18px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
input[type='text'],input[type='password'] {
  height: 45px;
  width: 100%;
  margin-bottom: 10px;
  box-sizing: border-box;
  padding: 0px 3px;
  background: #fff;
  border: 1px solid #d0d0d0;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 6px;
  font-size: 16px;
}
.loginBtn{
  height: 35px;
  box-sizing: border-box;
  padding: 4px 10px;
  background-color: #02660a;
  border: 1px solid #d0d0d0;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  cursor: pointer;
  margin: 15px;
  color: #d0d0d0;
}
.loginBtn:hover{
  background:  #2d6358;
}
.registerBtn{
  float: right;
  background: #02660a;
  color: #fff;
  height: 35px;
  box-sizing: border-box;
  padding: 4px 10px;
  border: 1px solid #d0d0d0;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: -50px;
  margin-right: 10px;
}
.header a.logo {
  height: 35px;
  width: 40px;
  background-size: cover;
  background-image: url(pinkwomensilhouettewomensday_386782022.png);
  border-radius : 2px;

}
.header a.logo:hover{
  opacity: 0.95;
}
.registerBtn:hover{
  background: #2d6358;
}
.Error{
  padding: 10px;
  background-color: #feeaea;
  border: 1px solid #fadadb;
  margin-bottom: 10px;
  border-radius: 3px;
  color: #02660a;
}
.header {
  margin-top: 2px;
  overflow: hidden;
  padding: 5px 20px;
  font-family: cursive;
  background-color: rgba(0, 0, 0, 0.9);

}
.header a {
  float: left;
  color: whitesmoke;
  text-align: center;
  padding: 10px;
  text-decoration: none;
  font-size: 20px; 
  line-height: 35px;
  border-radius: 4px;
  margin-right: 5px;
}
.header a:hover {
  background-color: #ddd;
  color: black;
}

.header a.active {
  background-color: dodgerblue;
  color: white;
}

.header-right {
  float: right;
}
.header-left {
  float: left;
  font-size: 50px;
  width: fit-content;
  margin: 0px;
}
